html {
  font-family: 'Georama', sans-serif;
  font-size: 16px;
}

* {
  box-sizing: border-box;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
  background-color: burlywood;
}

.container {
  max-width: 90%;
  background-color: white;
  margin-left: auto;
  margin-right: auto;
  height: 600px;
}

.receipeRow {
  width: 100%;
  display: flex;
  align-items: center;
  padding: 15px;
}

.receipeRow-image {
  width: 10%;
  padding-left: 10px;
}

.receipeRow-image img {
  width: 100%;
}





